"use client";
import React, { useMemo, useEffect, useState } from "react";
import { Accordion, AccordionHeader, AccordionBody } from "./Accordion";

function Icon({ id, open }) {
  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      fill="none"
      viewBox="0 0 24 24"
      strokeWidth={2}
      stroke="currentColor"
      className={`${
        id === open ? "rotate-180" : ""
      } h-5 w-5 transition-transform`}
    >
      <path
        strokeLinecap="round"
        strokeLinejoin="round"
        d="M19.5 8.25l-7.5 7.5-7.5-7.5"
      />
    </svg>
  );
}

export default function Roadmap() {
  const [steps, setSteps] = useState([]);
  const [error, setError] = useState(null);
  const [openId, setOpenId] = useState(null);

  const handleOpen = (value) => setOpenId(openId === value ? 0 : value);

  useEffect(() => {
    const fetchRoadmap = async () => {
      try {
        const response = await fetch("https://51.20.43.58/api/v1/roadmap");
        const data = await response.json();
        setSteps(data);
      } catch (error) {
        console.error("Error fetching roadmap data:", error);
        setError("Failed to load roadmap data.");
      }
    };

    fetchRoadmap();
  }, []);

  const activeIndex = useMemo(() => {
    return steps.findIndex((s) => !s.isCompleted);
  }, [steps]);

  return (
    <section className="py-10 map-gradient overflow-x-hidden">
      <div className="max-w-5xl mx-auto px-4">
        <h2 className="text-3xl sm:text-4xl font-bold text-center mb-8 text-gray-900">
          Loyihaning Roadmapi
        </h2>
        {error && <div className="text-red-500 text-center mb-4">{error}</div>}
        <div className="grid gap-6">
          {steps.map((step) => (
            <Accordion
              key={step.id}
              open={openId === step.id}
              className="rounded-2xl border bg-white"
            >
              <AccordionHeader
                onClick={() => handleOpen(step.id)}
                className="px-6 py-4"
              >
                <div className="flex items-center gap-4">
                  <div
                    className={`flex items-center justify-center w-11 h-11 rounded-lg font-semibold text-sm ${
                      step.isCompleted
                        ? "bg-gray-200 text-gray-500"
                        : "bg-blue-500 text-white"
                    }`}
                  >
                    {step.id}
                  </div>
                  <div>
                    <h3 className="text-lg sm:text-xl font-semibold">
                      {step.title}
                    </h3>
                    <p className="text-sm text-gray-500">{step.description}</p>
                  </div>
                </div>
              </AccordionHeader>
              <AccordionBody className="px-6 py-4">
                <div className="space-y-4">
                  <h4 className="font-medium text-gray-700">Tasks:</h4>
                  <ul className="space-y-2">
                    {step.tasks?.map((task) => (
                      <li key={task.id} className="flex items-center gap-2">
                        <input
                          type="checkbox"
                          checked={task.completed}
                          readOnly
                          className="rounded text-blue-500"
                        />
                        <span
                          className={
                            task.completed ? "line-through text-gray-500" : ""
                          }
                        >
                          {task.task}
                        </span>
                      </li>
                    ))}
                  </ul>
                  <p className="mt-4 font-medium text-gray-700">
                    Expected Result: {step.result}
                  </p>
                </div>
              </AccordionBody>
            </Accordion>
          ))}
        </div>
      </div>
    </section>
  );
}
