"use client";
import Image from "next/image";
import banduImg from "../../public/welcome.png";
import { useEffect, useState } from "react";

export default function WelcomeSection() {
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    const timer = setTimeout(() => {
      setIsVisible(true);
    }, 100); // Delay for fade-in effect
    return () => clearTimeout(timer);
  }, []);

  return (
    <section className="bandu-gradient flex bg-white items-center justify-center min-h-[100vh] w-full overflow-hidden">
      <div
        className={`flex flex-col-reverse md:flex-row items-center justify-center px-4 md:px-8 lg:px-16 ${
          isVisible ? "opacity-100" : "opacity-0"
        }`}
      >
        <div className="w-full md:w-2xl text-center md:text-left mt-8 md:mt-0">
          <h1
            className="text-4xl sm:text-5xl md:text-6xl lg:text-[80px] font-medium mb-4 md:mb-0 md:mr-6 leading-[1.3]"
            style={{ fontFamily: "var(--font-inter)" }}
          >
            Banduga{" "}
            <span className="interactive-gradient">Hush</span>

            <style jsx>{`
              .interactive-gradient {
                display: inline-block;
                background-image: linear-gradient(90deg, #FFD54A, #FF6B6B, #050505);
                background-size: 200% 200%;
                -webkit-background-clip: text;
                background-clip: text;
                color: transparent;
                animation: hueShift 8s linear infinite;
              }

              @keyframes hueShift {
                0% { background-position: 0% 50%; }
                50% { background-position: 100% 50%; }
                100% { background-position: 0% 50%; }
              }
            `}</style>
            <br /> Kelibsiz
          </h1>
          <p
            className="mt-2 text-sm sm:text-base md:text-lg lg:text-xl text-[#000000]/80 font-medium leading-relaxed mx-auto md:mx-0"
            style={{
              fontFamily: "var(--font-open-sans)",
              maxWidth: "400px",
              width: "100%",
            }}
          >
            Bandu startap rivojlanish yo'l xaritasi â€” foydalanuvchi uchun qulay,
            tezkor va samarali xizmatlarni birlashtiruvchi tizim.
          </p>
        </div>
        <Image
          src={banduImg}
          alt="Bandu Logo"
          width={420}
          height={420}
          className="w-[280px] md:w-[340px] lg:w-[420px] h-auto mb-6 md:mb-0"
          priority
        />
      </div>
    </section>
  );
}